





















import Vue from "vue";
import Component from "vue-class-component";
import { PostModel } from "@/service/interfaces";
import { AppService } from "@/service/app.service";
import { ApiService } from "@/service/api.service";
import PostPreviewComponent from "@/components/PostPreview.vue";

@Component({
  components: {
    PostPreviewComponent,
  },
})
export default class PostList extends Vue {
  posts: Array<PostModel> = [];
  category = "";
  app = AppService.instance;
  public api: ApiService = ApiService.instance;

  mounted(): void {
    this.category = this.$route.params.category;
    this.loadPosts();
  }

  async loadPosts(): Promise<void> {
    try {
      this.posts = await this.app.api.postList({ categoryId: this.category });
      // console.log(this.posts);
    } catch (error) {
      this.app.error(error);
    }
  }
}
